@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
.Map {
  position: relative;
  width: 544px;
  height: 726px;
}
.Map__container {
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 10px;
}

.Map__placesCounters {
  position: absolute;
  bottom: 40px;
  right: 20px;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: #222;
  font-style: normal;
  font-weight: 500;
  line-height: 23px;
  letter-spacing: 0em;
  text-align: left;
  padding: 8px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.8);
}

.Marker {
  height: 8px;
  width: 8px;
}
.Marker_icon::after {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  color: blue;
  opacity: 0.3;
}

/*marker svg*/
circle {
  stroke: blue;
  stroke-width: 2px;
  stroke-opacity: 1;
  fill: blue;
}

.pulse {
  fill: white;
  fill-opacity: 0;
  transform-origin: 50% 50%;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes pulse {
  from {
    stroke-width: 3px;
    stroke-opacity: 1;
    transform: scale(0.3);
  }
  to {
    stroke-width: 0;
    stroke-opacity: 0;
    transform: scale(2);
  }
}

@keyframes pulse {
  from {
    stroke-width: 3px;
    stroke-opacity: 1;
    transform: scale(0.3);
  }
  to {
    stroke-width: 0;
    stroke-opacity: 0;
    transform: scale(2);
  }
}

.CardMap {
  width: 255px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  /* transform: translate(-50%, 10%); */
  background: #ffffff;
  box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.09);
  border-radius: 8px;
  /* position: absolute; */
  font-family: Roboto;
}
.CardMap__images .CardMap__image {
  width: 100%;
  height: 150px;
  border-radius: 8px 8px 0 0;
}

.CardMap__content {
  padding: 10px 10px 15px 15px;
  text-align: left;
}
.CardMap__objectQuantity {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #0076c1;
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.CardMap__objectQuantity:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #0076c1;
  bottom: -2px;
  left: 0;
}
.CardMap__address {
  font-size: 12px;
  font-weight: 400;
  color: #7b7b7b;
  margin-top: 11px;
}

.CardMap__objectArea {
  margin-top: 5px;
  font-size: 12px;
  color: #7b7b7b;
}

.CardMap__address .CardMap__objectArea {
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #7b7b7b;
}
.CardMap__prices {
  margin-top: 11px;
}
.CardMap__prices > * + * {
  margin-top: 7px;
}
.CardMap__price {
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #000;
}

.CardMap__prices > .CardMap__price:last-of-type {
  margin-left: 16px;
}

.CardMap__price > span {
  margin-right: 5px;
}

.CardMap__title {
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  font-family: Roboto;
}

.CardMap__order {
  background: #0076c1;
  border-radius: 20px;
  color: #fff;
  border: none;
  padding: 6px 16px;
  margin-top: 15px;
}

.CardMap__images {
  display: flex;
  flex-direction: column;
}
.CardMap__imageContainer {
  position: relative;
}

.CardMap__button {
  position: absolute;
  top: 50%;
}

.CardMap__button:first-of-type {
  left: 10px;
}

.CardMap__button:last-of-type {
  right: 10px;
}

.CardMap__imageButtonPagination {
  display: flex;
  padding: 0 1.5px;
  margin-top: 3px;
}

.CardMap__imageButtonPaginationControll {
  border-radius: 4px;
  border: none;
  height: 20px;
  width: 60px;
  margin: 0 1.5px;
  background: #c0c0c0;
}
.CardMap__imageButtonPaginationControllActive {
  background: #323232;
}

.infoBox > img {
  display: none;
}

