.CardMap__infoBox__leftDown {
  transform: translate(-50%, -25%) !important;
  left: 0 !important;
  top: 0 !important;
}

.CardMap__infoBox__rightUp {
  transform: translate(5%, -50%) !important;

  left: 0 !important;
  top: 0 !important;
}

.CardMap__infoBox__rightDown {
  transform: translate(-50%, -25%) !important;
  left: 0 !important;
  top: 0 !important;
}

.CardMap__objectPropertes {
  font-size: 12px;
  font-weight: 400;
  color: #7b7b7b;

  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 20px;
  padding-bottom: 25px;
  grid-row-gap: 15px;
}
.CardMap__objectProperty {
  display: flex;
  align-items: center;
}
.CardMap__objectProperty span {
  margin-left: 4px;
  font-size: 12px;
  font-weight: 400;
}

.sliders {
  display: flex;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100%;
  overflow: auto;
}

.sliders > * {
  height: 100%;
  width: 100%;
  padding: 20px;
  color: #fff;
  flex-shrink: 0;
}

.Slider__slide {
  width: 100%;
  height: 100%;
}

.Slider__slide > * {
  width: 100%;
  height: 100%;
}

.awssld__bullets button {
  width: 25%;
  height: 2px;
}

.awssld__bullets--active {
  height: 2px;
  padding: 4px;
}

.Environment {
  display: flex;
  width: 160px;
  flex-direction: column;
  text-align: center;
  flex: 0 0 auto;
  margin-top: 16px;
}
.Environment__image {
  width: 100%;
}
.Environment__image > img {
  width: 100%;
}

.Environment__title {
  margin-top: 10px;
  font-family: Geometria;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 23px;
  letter-spacing: 0em;
  text-align: center;
  color: #000000;
}
.Environment__description {
  margin-top: 3px;
  font-family: Geometria;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0em;
  color: #858585;
}

.Modal__container {
  position: fixed !important;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  height: 100%;
  top: 0;
  right: 0;
  left: 0;
  overflow-y: auto;

  background-color: #fff;
}

.Modal__wrap {
  flex: 1 1;
  margin: 64px auto;
}

.Modal__inner-wrap {
  height: 100%;
  display: flex;
  align-items: center;
}

.Modal {
  position: relative;
}

.Modal__content {
  border-radius: 8px;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.08);
}

.Modal__close-icon {
  position: absolute;
  top: 31px;
  right: 31px;
  width: 18px;
  height: 18px;

  z-index: 1000;

  cursor: pointer;
}

.Modal__close-icon:hover {
  fill: #333333;
}

.Modal__contentWrap {
  max-width: calc(1116px + 128px);
  margin-left: auto;
  margin-right: auto;
  /* padding-left: 64px;
  padding-right: 64px; */
  font-family: Roboto;
}

@media (max-width: 766px) {
  .Modal__contentWrap {
    /* padding-left: 0;
    padding-right: 0; */
  }
  .Modal__wrap {
    margin: 0;
  }
}

.MapContainer {
  height: 60vh;
}

.MapModal {
  position: relative;
  width: 544px;
  height: 726px;
}
.MapModal__container {
  width: 100%;
  height: 100%;
  position: relative;
}

.MapModal__map {
  height: 654px;
  width: 918px;
  left: 220px;
  top: 70px;
  border-radius: 10px;
}

.ModalMap__Content {
  width: calc(100vw - 64px);
  max-width: 1244px;
}

/* .ModalMap__Content > * {
  border: none !important;
}

.ModalMap__Content > div {
  width: 100% !important;
} */

.ModalMap__map {
  width: 100%;
  border: none !important;
  /* height: calc(100vh - 68px); */
}
.ModalMap__map > div {
  width: 100% !important;
}

.ModalMap__map > * {
  border: none !important;
}

.ModalMap__NavigationMenu {
  display: flex;
  padding: 8px 8px 0px;
  align-items: center;
}

.ModalMap__links {
  width: 100%;
  height: 60px;

  border-bottom: 1px solid rgb(232, 233, 234);
  display: flex;
  align-items: center;
  text-decoration: none;
  max-width: 1080px;
  overflow: auto;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}
.ModalMap__links::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.ModalMap__link {
  padding: 8px 16px;
  list-style-type: none;
  box-sizing: border-box;
}

.ModalMap__linkButton {
  margin: 0px;
  border-radius: 8px;
  cursor: pointer;
  display: inline-block;
  color: rgb(59, 65, 68);
  text-align: center;
  font-weight: bold;
  transition: top 0.1s ease 0s, box-shadow 0.1s ease 0s,
    border-color 0.1s ease 0s, background-color 0.1s ease 0s, color 0.1s ease 0s;
  white-space: nowrap;
  font-size: 16px;
  line-height: 1.5;
  padding: 8px 16px;
  outline: none;
}

.ModalMap__closeMap {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 2.35%;
  background: transparent;
  border: none;
}

.ModalMap__paginationButton {
  display: none;
}

.ModalMap_leftArrow {
  transform: rotate(180deg);
  height: 16px;
}
.ModalMap_rightArrow {
  height: 16px;
}
.ModalMap__closeMapIcon {
  height: 24px;
}

.ModalMap__linkButton_inactive {
  border: none;
  outline: none;
  background-color: transparent;
}

.ModalMap__linkButton_inactive:hover {
  background-color: rgb(232, 233, 234);
}

.ModalMap__linkButton_active {
  color: rgb(0, 120, 130);
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(59, 65, 68, 0.4) 0px 8px 20px -15px;
  border: solid 1px rgb(232, 233, 234);
}

@media (max-width: 968px) {
  .ModalMap__paginationButton {
    display: flex;
    height: 24px;
    width: 24px;
    border-radius: 50%;
    background: transparent;
    border: 1px solid #cfcfcf;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 12px;
    margin-right: 12px;
  }
}

@media (max-width: 766px) {
  .ModalMap__Content {
    width: 100vw;
    height: 100vh;
  }

  .ModalMap__map > div {
    width: 100% !important;
    height: calc(100vh - 68px) !important;
  }
}

@media (max-width: 568px) {
  .ModalMap__link .ModalMap__linkButton {
    padding: 8px;
  }
}

