.body {
  overflow-x: none;
}
.Environments {
  margin-top: 48px;
}

.Environments__wrapper {
  /* margin-top: 16px;
  display: flex;
  justify-content: space-between; */

  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;

  justify-content: space-between;
}

/* .Environments__wrapper > * + * {
  margin-left: 78px;
} */
/* Hide scrollbar for Chrome, Safari and Opera */
.Environments__wrapper::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.Environments__wrapper {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
